import React, { Fragment } from 'react';
import Helmet from 'react-helmet';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { withReduxSaga } from '../../../store';
import withRoot from '../../withRoot';

import Typography from 'material-ui/Typography';
import { withStyles } from 'material-ui/styles';
import Button from 'material-ui/Button';
import HomeIcon from 'material-ui-icons/Home';
import MyGramIcon from '../../../static/svgcomp/MyGram';

const styles = () => ({
  root: {
    background: 'rgba(0,0,0,0.03)',
    minHeight: '1000vh',
  },
  head: {
    background: '#FFF',
    borderBottom: '1px solid rgba(0,0,0,.0975)',
    position: 'fixed',
    top: '0',
    width: '100%',
    zIndex: '1',
    WebkitTransition: 'height .2s ease-in-out',
    transition: 'height .2s ease-in-out',
  },
  headContent: {
    maxWidth: 1010,
    margin: '0 auto',
    padding: ' 0px 40px',
    position: 'relative',
    top: '50%',
    WebkitTransform: 'translateY(-50%)',
    MsTransform: 'translateY(-50%)',
    transform: 'translateY(-50%)',
    display: 'flex',
  },
  searchWrap: {
    WebkitBoxFlex: '0',
    WebkitFlex: '0 1 auto',
    MsFlex: '0 1 auto',
    flex: '0 1 auto',
    minWidth: '125px',
    width: '215px',
    height: '30px',
    position: 'relative',
    margin: '0 auto',
  },
  searchInput: {
    border: 'solid 1px #dbdbdb',
    borderRadius: '3px',
    color: '#999',
    fontSize: '14px',
    outline: 'none',
    padding: '3px 10px 3px 26px',
    zIndex: '2',
    width: 200,
    height: 28,
    paddingLeft: '80px',
    boxSizing: 'border-box',
    background: 'url(https://cdn3.iconfinder.com/data/icons/google-material-design-icons/48/ic_search_48px-128.png)',
    backgroundSize: '16px',
    backgroundRepeat: 'no-repeat',
    backgroundPosition: '62px 5px',
    backgroundColor: 'rgb(250, 250, 250)',
    '&:focus': {
      paddingLeft: '24px',
      backgroundPosition: '5px',
      backgroundColor: '#fff',
    },
  },
});

class Index extends React.Component {

  constructor(props) {
    super(props);
    this.handleScroll = this.handleScroll.bind(this);
    this.state = {
      dibawah: false,
      data: false,
    };
  }

  componentDidMount = () => {
    window.addEventListener('scroll', this.handleScroll);
  }
  
  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
  }

  handleScroll() {
    let scrOfY = 0;
    let dibawah = false;
    if (typeof (window.pageYOffset) === 'number') {
      // Netscape compliant
      scrOfY = window.pageYOffset;
      dibawah = window.pageYOffset > 50;
      
    } else if (document.body && document.body.scrollTop) {
      // DOM compliant
      scrOfY = document.body.scrollTop;
      dibawah = document.body.scrollTop > 50;
    }
    // return scrOfY;
    return this.setState({ dibawah });
  }

  render() {
    const { classes, title } = this.props;
    console.log(this.props || 'ss')
    return (
      <Fragment>
        <Helmet defaultTitle="adnanfajlur">
          <title>adnanfajlur</title>
          <html lang="en" />
        </Helmet>
        <div className={classes.root}>
          <div className={classes.head} style={{ height: dibawah ? 52 : 77 }}>
            <div className={classes.headContent}>
              <MyGramIcon style={{ width: 30, height: 30 }} />
              <div className={classes.searchWrap}>
                <input
                  placeholder="Search"
                  className={classes.searchInput} />
              </div>
            </div>
          </div>
        </div>
      </Fragment>
    );
  }
}

Index.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withRoot(withStyles(styles)(Index));



